import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { Elements, CardElement } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import SocketClient from "../../utils/socketClient";
import CheckoutForm from "../checkoutform";
import "./style.css";

const api_token = "cc638af3ea2783059aae7e32b5b80e34c1f0d1f4";
// stripe = Stripe('pk_live_3LlO84dRneuvLT4cRNrqNI1a00zer0Zu1K');
const stripePromise = loadStripe(
  "pk_test_51GsJaTENKR3DelSmluW4kP0QbP1fldW9sVMuegxwyMbmBt7yduvqb3Y0uvvNyZv9Da3gLS5o8iv25LSGvGOWYxDk009lbSNn9f"
);

const Credit = ({ props }) => {
  const dispatch = useDispatch();
  const [amount, setAmount] = useState(1000);

  useEffect(() => {}, []);
  const socket = useSelector((state) => state.socket);
  function handleChange(e) {
    setAmount(e.target.value);
  }
  const ELEMENTS_OPTIONS = {
    fonts: [
      {
        cssSrc: "https://fonts.googleapis.com/css?family=Roboto",
      },
    ],
  };
  return (
    <div>
      <div>
        <div class="mt-4">
          <b>Pipedrive User: </b>
          {socket.user.name}
        </div>
        <div class="mt-4">
          <b>Organisztion: </b>
          {socket.user.company_name}
        </div>
        <div class="mt-4">
          <b>Credits: </b>
          {SocketClient.getInstance().numberWithCommas(socket.credits)}
        </div>
        {/* <button class="mt-4" style={{background: '#54807e', color: 'white'}}> BUY MORE CREDITS</button> */}
      </div>
      
      <div class="box">
        <select value={amount} onChange={handleChange} style={{width:'100%'}}>
          <option value="1000">1,000 credits for US $8</option>
          <option value="5000">5,000 credits for US $39</option>
          <option value="25000">25,000 credits for US $169</option>
        </select>
      </div>
      <div>
        <Elements stripe={stripePromise} options={ELEMENTS_OPTIONS}>
          <CheckoutForm amount={amount} />
        </Elements>
      </div>
    </div>
  );
};

export default Credit;
